        <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">'chevron-up'</span>&gt;</span>上一页<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">'chevron-down'</span>&gt;</span>下一页<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-button-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="button-shu-xing"><a class="header-anchor" href="#button-shu-xing" aria-hidden="true">¶</a> button 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td>string</td>
<td>default / primary / success / warning / error / info / text</td>
<td>default</td>
</tr>
<tr>
<td>plain</td>
<td>是否朴素按钮</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>round</td>
<td>是否圆形按钮</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>dash</td>
<td>是否边框为虚线（在<code v-pre="">plain</code>模式下使用，否则可能看不到效果）</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>loading</td>
<td>是否加载中状态</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用状态</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>icon</td>
<td>图标类名</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>native-type</td>
<td>原生 type 属性</td>
<td>string</td>
<td>button / submit / reset</td>
<td>button</td>
</tr>
</tbody>
</table>
<h2 id="button-fang-fa"><a class="header-anchor" href="#button-fang-fa" aria-hidden="true">¶</a> button 方法</h2>
